.form_container12 {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    font-family: 'Lobster', cursive;

}

.form_container12 form {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    box-shadow: 5px 5px 10px rgba(12, 4, 4, 0.747);
    padding: 1rem 3rem;
    background-color: rgb(5, 80, 141);
    ;
    border-radius: 1rem 0rem;


}

.form_container12  label {
    font-size: 20px;
    color: whitesmoke;
}

.form_container12 form input {
    padding: 0.5rem 5rem;
    border: none;
    border-radius: 4px;

}

.btn12 {
    color: rgb(255, 255, 255);
    background-color: rgb(171, 7, 236);
    padding: 1rem 14rem;
    border-radius: 3px;
}

.budget-container12 span {
    margin: 0.5rem;
    font-size: 1.1rem;
    color: whitesmoke;

}


@media screen and (max-width:900px) {
    form {

        margin: 2rem;
        padding: 2rem 5rem;
    }

    form input,
    textarea {
        padding: 0.5rem 7rem;
    }

    .btn12 {
        padding: 1rem 11rem;
    }


}

@media screen and (max-width:650px) {
    form {

        margin: 2rem;
        padding: 1rem 1rem;
    }


}

@media screen and (max-width:580px) {

   

    .form_container12 form input,
    textarea {
        padding: 0.5rem 3rem;
       
    }

    .btn12 {
        padding: 1rem 3rem;
    }


}

@media screen and (max-width:512px) {


    .form_container12 form input,
    textarea {
        padding: 0.5rem 1rem;
    }

    .btn12 {
        padding: 1rem 1rem;
    }



}

@media screen and (max-width:448px) {


    form label {
        font-size: 20px;
    }

    .btn12 {
        padding: 1rem 1rem;
    }



}


@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');