nav {
    width: 100%;
    height: 7rem;
    padding: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 5px 5px 10px rgba(22, 1, 1, 0.747);
    background-color: rgb(5, 80, 141);
    // background-color: rgba(25, 14, 185, 0.986);
    // box-shadow: 0 0 10px rgba(22, 1, 1, 0.747);
    // background-color: white;
    position: fixed;

}

// eslint-disable-next-line

.nav-link {
    font-family: 'Lobster', cursive;
    font-size: 1.5rem;
    padding-left: 1rem;
}

.nav-link a {
    margin: 0 1rem;
    transition: all 1s;
}
.nav-link a:hover {
    color: pink;
    text-decoration: underline;
}

.logo{
  box-shadow: rgba(230, 15, 176) 0px 50px 100px -20px,
            rgb(72, 212, 7) 0px 30px 60px -30px;
}

.logo img{
   width: 60%;
   border-radius:0 10px;
   
}


.togglebar {
    display: none;
}


@media screen and (max-width:1000px) {

    .headerApp {
        height: 7rem;
    }


    .togglebar {
        display: block;
        color: antiquewhite;
        font-size: 2rem;
    }

    .nav-link {
        display: none;
    }

    .mobile-menu-link {
        position: absolute;
        right: 0;
        background-color: rgb(5, 80, 141);
        font-size: 150%;
        margin-top: 70%;
        width: 100%;
        border-radius: 0 40px;
        display: flex;
        flex-direction: column;
        // align-items: center;
        padding:10% 0;
        transition: all 2s;
        transform-origin: left;
        box-shadow: rgba(230, 15, 176) 0px 50px 100px -20px,
            rgb(72, 212, 7) 0px 30px 60px -30px;
    }

    

    .mobile-menu-link a {
        height: 2rem;
        padding: 3rem;
        display: flex;
        justify-content: space-around;
        flex-direction: column;
    }

}

