.container1 {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.mydiv3,
.mydiv2,
.mydiv1 {
    font-family: 'Lobster', cursive;
    font-size: 3rem;
}

.mydiv1 {
    color: rgb(250, 251, 252);
    position: absolute;
    top: 70%;
    left: 30%;
}

.mydiv3 {
    color: rgb(222, 232, 247);
    position: absolute;
    top: 40%;
    right: 5%;
}

.mydiv2 {
    color: rgb(141, 5, 62);
    position: absolute;
    top: 40% !important;
    left: 5% !important;
}


.box2 {

    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(255, 255, 255, 0.123);
    width: 70%;
    font-family: 'Lobster', cursive;
}


.pic1 img {
    width: 100%;
    height: 30%;
}

.pic1{
    z-index: -1;
}

.course {
    display: flex;
    font-family: 'Lobster', cursive;
    text-shadow: 1px 2px rgba(199, 5, 224, 0.747);
    margin-top: 1.5rem;
    font-size: 2.5rem;
    flex-direction: column;
    color: rgb(5, 80, 141);
}

.course hr {
    align-self: center;
    color: rgb(160, 13, 13);
    width: 700%;
}

.icons {
    font-size: 4rem;
    transition: all 1s;

}

.icons:hover {
    font-size: 6rem;
    color: rgb(212, 130, 171);
}


.box1 {
    margin: 1rem;
    width: 80%;
    height: 15rem;
    border-radius: 1rem;
    box-shadow: 1px 2px 1px 2px rgba(189, 141, 141, 0.747);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    // color: rgb(10, 10, 82);
    color: antiquewhite;
    background-color: rgb(5, 80, 141);
    transition: all 1s;
}

.box1:hover {
    width: 100%;
    height: 15rem;
    background-color: rgb(9, 86, 150);
}


.box1 img {
    width: 100%;
    height: 5rem;
}

.box1 h2 {
    font-size: 30px;
    margin-bottom: 1rem;

}

.box1 a:hover {
    color: rgb(226, 89, 158) !important;
    transition: all;
}

.heading1 {
    color: rgb(12, 12, 128);
    margin: 0 6rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-size: 1.2rem;
}

.heading1 h1 {
    font-family: 'Lobster', cursive;
    display: flex;
    background-color: rgb(5, 80, 141);
    box-shadow: 1px 2px 1px 2px rgba(189, 96, 218, 0.747);
    width: 80%;
    justify-content: center;
    align-items: center;
    margin: 3rem;
    color: aliceblue;
}

.heading1 p {
    align-self: flex-start;
}


@media screen and (max-width:1000px) {
    .box2 {

        display: flex;
        align-items: center;
        justify-content: center;
        width: 70%;
        flex-wrap: wrap;
    }

}

@media screen and (max-width:550px) {
    .heading1 h1 {
        font-family: 'Lobster', cursive;
        display: flex;
        width: 80%;
        justify-content: center;
        text-align: center;
        font-style: 1rem;
        margin: 3rem;
        color: aliceblue;
    }

    .mydiv3,
    .mydiv2,
    .mydiv1 {
        font-size: 0.2rem;
    }
}

