.intro {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 0 5rem;

}

.courseForMbbs {
    display: flex;
    font-family: 'Lobster', cursive;
    text-shadow: 1px 2px rgba(189, 141, 141, 0.747);
    margin-top: 1.5rem;
    font-size: 2.5rem;
    flex-direction: column;
}

ul li {
    font-weight: 600;
}

.top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
    flex-wrap: wrap;
}

.section1 {
    display: flex;
    justify-content: space-between;
    // align-items: center;
    padding: 2rem;
    flex-wrap: wrap;
}

.mbbsInState {
    background-color: rgb(5, 80, 141);
    width: 85%;
    padding: 1.5rem;
    font-size: 2rem;
    font-family: 'Lobster', cursive;
    box-shadow: 1px 2px 1px 2px rgba(189, 96, 218, 0.747);
    border-radius: 0 12px;
}

.mbbsInState span {
    text-shadow: 1px 2px rgba(189, 141, 141, 0.747);
    margin: 0.6rem;
}

.mbbsInState span a {
    color: whitesmoke;
    transition: all 1s;
}

.mbbsInState span a:hover {
    color: rgb(230, 171, 171);
    font-size: 2.5rem;

}

.section1-left {
    width: 500px;
    padding: 1rem;
}

.section1-right {
    width: 500px;
}

.Heading h1 {
    color: rgba(201, 0, 0, 0.932);
    font-family: 'Lobster', cursive;
    font-size: 3.5rem;
    margin: 3rem;
}

.top ul li {
    font-family: 'Lobster', cursive;
    font-size: 2rem;
    color: rgb(5, 80, 141);
}

.free span {
    color: red;
    margin-top: 5rem;
    margin-bottom: 1rem;
    width: 15%;
    border: 2px solid red;
    padding: 0.3rem;
}

.num {
    margin-bottom: 1rem;
    font-size: 1.2rem;
    text-decoration: underline;
    font-weight: 800;
}

.section1-left h2 {
    color: red;
    font-family: 'Lobster', cursive;
    font-size: 3rem;

}

