.section2 {
    margin: 0 5rem;
}

@media screen and (max-width:1256px) {

    .intro {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin: 0 1rem;

    }

}

@media screen and (max-width:1130px){
    
    .top {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 2rem;
        flex-wrap: wrap;
    }
}

@media screen and (max-width:1094px) {

    .section1 {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 2rem;
        flex-wrap: wrap;
    }

    .section1-left {
        width: 85%;
        padding: 1rem;
    }
    
    .section1-right {
        width: 70%;
        background-color: antiquewhite;
    }
}

@media screen and (max-width:714px){

    .Heading h1 {
        font-size: 3rem;
        margin: 2rem 0;
    }

    .section1-left h2 {
        font-size: 2.5rem;
    
    }

    .mbbsInState {
        padding: 1rem;
        font-size: 1.1rem;
    }
}

@media screen and (max-width:600px){
    .Heading h1 {
        font-size: 2rem;
        margin: 2rem 0;
    }

    .top ul li {
        font-size: 1.5rem;
    }

    .section1-left h2 {
        font-size: 1.8rem;
    }
}

@media screen and (max-width:565px){

    .top2 img{
        width: 100%;
        height: 100%;
    }

    .section1-left {
        width: 95%;
        padding: 1rem;
    }
    
    .section1-right {
        width: 80%;
        background-color: antiquewhite;
    }
}
@media screen and (max-width:448px){

    .section1 {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0.3rem;
        flex-wrap: wrap;
    }

    .top2 img{
        width: 100%;
        height: 100%;
    }

    .section1-left {
        width: 95%;
        padding: 1rem;
    }
    
    .section1-right {
        width: 100%;
        background-color: antiquewhite;
    }
}

@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');