.form_container {
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Lobster', cursive;
    background-color: rgb(5, 80, 141);

}

.form_container form {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    box-shadow: 5px 5px 10px rgba(12, 4, 4, 0.747);
    margin: 2rem;
    padding: 1rem 5rem;
    background-color: white;
    border-radius: 1rem 0rem;
    color: rgb(5, 80, 141);


}

.form_container form label {
    font-size: 20px;
    color: rgb(5, 80, 141);
}

input[type=text],[type=email],[type=number] {
    border: none;
    padding: 0.5rem 3rem;
    border-bottom: 1px solid rgb(5, 80, 141);
    outline: none;
}

.btn1 {
    color: rgb(5, 80, 141);
    background-color: rgb(173, 10, 206);
    padding: 1rem 2rem;
    border-radius: 10px;
}
.btn1:hover {
    color: rgb(5, 80, 141);
    background-color: rgb(173, 10, 206);
    padding: 1rem 2rem;
    border-radius: 13px;
}

.btn1:focus{
    color: rgb(29, 93, 146);
    background-color: rgb(193, 64, 219);
    border-radius: 15px;
}


.budget-container span {
    color: rgb(5, 80, 141);
    margin: 0.5rem;
    font-size: 1.1rem;

}


@media screen and (max-width:900px) {
    form {
        margin: 2rem;
        padding: 2rem 5rem;
        border-radius: 1rem 0rem;


    }


    form label {
        font-size: 20px;
        color: blue;
    }

    form input,
    textarea {
        padding: 0.5rem 7rem;
        border: none;
        border-radius: 4px;

    }


    .btn1 {
        padding: 1rem 7rem;
        border-radius: 3px;
    }


}

@media screen and (max-width:650px) {
    .form_container form {

        margin: 1rem 2rem;
        padding: 1rem 1rem;
       
    }


}

@media screen and (max-width:530px) {
    form {

       
        margin: 2rem;
        padding: 1rem 1rem;
        border-radius: 1rem 0rem;


    }

    form input,
    textarea {
        padding: 0.5rem 5rem;
        border: none;
        border-radius: 4px;

    }

    .btn1 {
        padding: 1rem 5rem;
        border-radius: 3px;
    }


}

@media screen and (max-width:450px) {
    .form_container form {

       
        margin: 1rem;
        padding: 3rem 2rem;
        border-radius: 1rem 0rem;


    }


     input[type=text],[type=email],[type=number] {
        padding: 0 0;
    }

    .btn1 {
        padding: 1rem 2rem;
        border-radius: 2.5px;
    }


}


@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');