.karnataka{
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 0 5rem;
    
}

ol li{
    font-weight: 600;
}

.top-a{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
    flex-wrap: wrap;
}

.top-1{
    width: 500px;
    padding: 1rem;
}

.top-2{
    width: 500px;
}

.section-k{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
}

.section-k-left img{
    width: 100%;
}


.section-k-left h2{
    color:red;
    font-family: 'Lobster', cursive;
    font-size: 2.5rem;

}



@media screen and (max-width:1250px) {

    .karnataka{
        display:flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin: 0 1rem;
        
    }

    .top-a{
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 2rem;
        flex-wrap: wrap;
    }

    .top-1{
        width: 100%;
    }
    
  
}
