.aboutBox{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 0 5rem;
    color:rgb(5, 80, 141);
    font-family: 'Lobster', cursive;
}

.about-Heading h1{
    font-family: 'Lobster', cursive;
    font-size: 3.5rem;
    margin-top: 1rem;
}

.top-about{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
    flex-wrap: wrap;
    color: rgb(5, 80, 141);
}

.top-about-1{
    width: 500px;
    padding: 1rem;
}

.top-about-2{
    width: 500px;
}


@media screen and (max-width:1250px) {

    .aboutBox{
        display:flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin: 0 1rem;
        
    }

    .top-about{
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 2rem;
        flex-wrap: wrap;
    }

    .top-about-1{
        width: 100%;
    }
    
  
}

